import { Trabajador } from './trabajador';
import { Empresa } from './empresa';

export class Context {

  private _trabajador: Trabajador;

  private _empresa: Empresa;

  get trabajador(): Trabajador { return this._trabajador; }

  set trabajador(trabajador: Trabajador) { this._trabajador = trabajador; }

  get empresa(): Empresa { return this._empresa; }

  set empresa(empresa: Empresa) { this._empresa = empresa; }

  constructor(json: any) {
    this.trabajador = new Trabajador(json[0]);
    this.empresa = new Empresa(json[0]);
  }

}
