import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { OneSignal } from '@ionic-native/onesignal/ngx';
import { StatusBar } from '@ionic-native/status-bar/ngx';
import { Platform } from '@ionic/angular';
import { Environments } from 'src/app/commons/environment';
import { PAGE_TO_PUSH } from 'src/app/commons/preferences-keys';
import { ContextService } from 'src/app/services/context.service';
import { NetworkService } from './app/services/network.service';

@Component({
  selector: 'app-root',
  templateUrl: 'app.component.html',
  styleUrls: ['app.component.scss'],
})
export class AppComponent {
  constructor(
    private platform: Platform,
    private statusBar: StatusBar,
    private context: ContextService,
    private oneSignal: OneSignal,
    private router: Router,
    private networkService: NetworkService
  ) {
    this.initializeApp();
  }

  initializeApp() {
    /* cambiar Environments en DataService SST : PATH */
    this.context.setEnvironment(Environments.Prod);

    this.platform.ready().then(() => {
      // this.checkDarkTheme();
      this.statusBar.styleDefault();
      this.statusBar.overlaysWebView(false);

      this.oneSignalSetup();
      this.networkService.initializeNetworkEvents();
      this.networkService.testNetworkConnection();
    });
  }

  async checkDarkTheme(): Promise<void> {
    // let shouldAdd: boolean;
    // if (this.platform.is('android')) {
    //   shouldAdd = (await DarkMode.isDarkModeOn()).isDarkModeOn;
    // this.toggleDarkTheme(shouldAdd);
  }

  oneSignalSetup(): void {
    this.oneSignal.startInit(this.context.getOneSignalAppKey(), this.context.getGoogleProjectApiKey());
    this.oneSignal.inFocusDisplaying(this.oneSignal.OSInFocusDisplayOption.Notification);

    this.oneSignal.handleNotificationOpened().subscribe(result => {
      console.log(result);
      this.context.get().then(c => {
        if (result.notification.isAppInFocus || c !== undefined) {
          if (result.notification.payload.title === 'Rendición de Cuentas') {
            this.router.navigate(['/accountability']);
          } else {
            this.router.navigate(['/communications']);
          }
        } else {
          let pageToPush: string;
          if (result.notification.payload.title === 'Rendición de Cuentas') {
            pageToPush = '/accountability';
          } else {
            pageToPush = '/communications';
          }

          localStorage.setItem(PAGE_TO_PUSH, pageToPush);
        }
      });
    });

    this.oneSignal.endInit();
  }

  /**
   * Alterna el modo oscuro en base al parámetro indicado.
   *
   * @param shouldAdd Indica si activar o no el modo oscuro.
   */
  private toggleDarkTheme(shouldAdd: boolean): void {
    document.body.classList.toggle('dark', shouldAdd);
  }
}
