import { Entity } from './entity';

export class Trabajador extends Entity {
  get nombre(): string {
    return this._nombre;
  }

  set nombre(nombre: string) {
    this._nombre = nombre;
  }

  get tipoDocumento(): string {
    return this._tipoDocumento;
  }

  set tipoDocumento(tipoDocumento: string) {
    this._tipoDocumento = tipoDocumento;
  }

  get cargo(): string {
    return this._cargo;
  }

  set cargo(cargo: string) {
    this._cargo = cargo;
  }

  get correo(): string {
    return this._correo;
  }

  set correo(correo: string) {
    this._correo = correo;
  }

  get numeroTelefono(): string {
    return this._numeroTelefono;
  }

  set numeroTelefono(numeroTelefono: string) {
    this._numeroTelefono = numeroTelefono;
  }

  get sexo(): string {
    return this._sexo;
  }

  set sexo(sexo: string) {
    this._sexo = sexo;
  }

  get fechaNacimiento(): string {
    return this._fechaNacimiento;
  }

  set fechaNacimiento(value: string) {
    this._fechaNacimiento = value;
  }

  get idEPS(): string {
    return this._idEPS;
  }

  set idEPS(value: string) {
    this._idEPS = value;
  }

  get nombreEPS(): string {
    return this._nombreEPS;
  }

  set nombreEPS(value: string) {
    this._nombreEPS = value;
  }

  get direccion(): string {
    return this._direccion;
  }

  set direccion(value: string) {
    this._direccion = value;
  }

  get idOcupacion(): string {
    return this._idOcupacion;
  }

  set idOcupacion(value: string) {
    this._idOcupacion = value;
  }

  get nombreOcupacion(): string {
    return this._nombreOcupacion;
  }

  set nombreOcupacion(value: string) {
    this._nombreOcupacion = value;
  }

  get nombreDepartamento(): string {
    return this._nombreDepartamento;
  }

  set nombreDepartamento(value: string) {
    this._nombreDepartamento = value;
  }

  get nombreMunicipio(): string {
    return this._nombreMunicipio;
  }

  set nombreMunicipio(value: string) {
    this._nombreMunicipio = value;
  }

  get idMunicipio(): number {
    return this._idMunicipio;
  }

  set idMunicipio(value: number) {
    this._idMunicipio = value;
  }

  get idDepartamento(): number {
    return this._idDepartamento;
  }

  set idDepartamento(value: number) {
    this._idDepartamento = value;
  }

  get primerNombre(): string {
    return this._primerNombre;
  }

  set primerNombre(value: string) {
    this._primerNombre = value;
  }

  get segundoNombre(): string {
    return this._segundoNombre;
  }

  set segundoNombre(value: string) {
    this._segundoNombre = value;
  }

  get primerApellido(): string {
    return this._primerApellido;
  }

  set primerApellido(value: string) {
    this._primerApellido = value;
  }

  get segundoApellido(): string {
    return this._segundoApellido;
  }

  set segundoApellido(value: string) {
    this._segundoApellido = value;
  }

  get idRegistro(): string {
    return this._idRegistro;
  }

  set idRegistro(value: string) {
    this._idRegistro = value;
  }

  private _nombre: string;

  private _tipoDocumento: string;

  private _cargo: string;

  private _correo: string;

  private _numeroTelefono: string;

  private _sexo: string;

  private _fechaNacimiento: string;

  private _idEPS: string;

  private _nombreEPS: string;

  private _direccion: string;

  private _idOcupacion: string;

  private _nombreOcupacion: string;

  private _nombreDepartamento: string;

  private _nombreMunicipio: string;

  private _idMunicipio: number;

  private _idDepartamento: number;

  private _primerNombre: string;

  private _segundoNombre: string;

  private _primerApellido: string;

  private _segundoApellido: string;

  private _idRegistro: string;

  static parse(json: any): Trabajador {
    return new Trabajador(json);
  }

  constructor(json: any) {
    super(parseInt(json.idPersona, 10));

    this.nombre = json.nombre1 + ' ' + json.apellido1 + ' ' + json.apellido2;
    this.tipoDocumento = json.tipoDoc;
    this.cargo = json.cargo;
    this.correo = json.emailPersona;
    this.numeroTelefono = json.telPersona;
    this.sexo = json.sexoPersona;
    this.fechaNacimiento = json.fechaNacimiento;
    this.idEPS = json.idEps;
    this.nombreEPS = json.nombreEps;
    this.direccion = json.dirPersona;
    this.idOcupacion = json.idOcupacion;
    this.nombreOcupacion = json.ocupacion;
    this.nombreDepartamento = json.nomDepAfiliado;
    this.nombreMunicipio = json.nomMunAfiliado;
    this.idMunicipio = json.idMunAfiliado;
    this.idDepartamento = json.idDepAfiliado;
    this.primerNombre = json.nombre1;
    this.segundoNombre = json.nombre2;
    this.primerApellido = json.apellido1;
    this.segundoApellido = json.apellido2;
    this.idRegistro = json.idRegistro;
  }
}
