import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController, NavParams } from '@ionic/angular';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';

@Component({
  selector: 'app-modal-add-trabajador',
  templateUrl: './modal-add-trabajador.component.html',
  styleUrls: ['./modal-add-trabajador.component.scss'],
})
export class ModalAddTrabajadorComponent implements OnInit {
  trabajador: TrabajadorMigrado;

  constructor(public modalController: ModalController, private navParams: NavParams, private alerController: AlertController) {}

  ngOnInit() {
    this.trabajador = this.navParams.get('trabajador');
    console.log('trabajador en modal _DDDDDD' + JSON.stringify(this.trabajador));
    this.trabajador.accion = true;
  }

  dismissModal() {
    /* limpiar user */
    this.modalController.dismiss();
  }

  add() {
    if (this.trabajador.accion == true || this.trabajador.blnAyudanteSeguridad == true) {
      this.trabajador.blnActivo = true;
      this.modalController.dismiss({
        trabajador: this.trabajador,
      });
    } else {
      this.alerController
        .create({
          header: 'Alerta',
          message: 'Debes seleccionar un roll Ayudante y/o Trabajador',
          buttons: ['Aceptar'],
        })
        .then(alert => {
          alert.present();
        });
    }
  }

  onChangeAyudante(event) {
    this.trabajador.blnAyudanteSeguridad = event.detail.checked;
  }

  onChangeTrabajador(event) {
    this.trabajador.accion = event.detail.checked;
    console.log(this.trabajador);
  }
}
