import { ESTADO_MENSAJE } from '../commons/enum-estado-mensaje';
import { Entity } from './entity';

export class Mensaje extends Entity {
  get asunto(): string {
    return this._asunto;
  }

  set asunto(asunto: string) {
    this._asunto = asunto;
  }

  get texto(): string {
    return this._texto;
  }

  set texto(texto: string) {
    this._texto = texto;
  }

  get fechaHoraEnvio(): Date {
    return this._fechaHoraEnvio;
  }

  set fechaHoraEnvio(fechaHoraEnvio: Date) {
    this._fechaHoraEnvio = fechaHoraEnvio;
  }

  get estado(): ESTADO_MENSAJE {
    return this._estado;
  }

  set estado(estado: ESTADO_MENSAJE) {
    this._estado = estado;
  }

  private _asunto: string;

  private _texto: string;

  private _fechaHoraEnvio: Date;

  private _estado: ESTADO_MENSAJE;

  static parse(json: any[]): Mensaje[] {
    return json.map(item => new Mensaje(item));
  }

  constructor(json: any) {
    super(json.PK_Id_Mensaje);
    this._asunto = json.Titulo;
    this._texto = json.Asunto.replace(new RegExp('</?strong>', 'g'), '');
    const textarea = document.createElement('textarea');
    textarea.innerHTML = this._texto;
    this._texto = textarea.value;
    const fecha = json.FechaEnvio.split(' ')[0].split('/');
    const horaAndFormat = json.FechaEnvio.split(' ');
    horaAndFormat.shift();
    const hora = horaAndFormat[0].split(':');

    this._fechaHoraEnvio = new Date(
      fecha[2],
      fecha[1] - 1,
      fecha[0],
      horaAndFormat[1] === 'a.' ? hora[0] : hora[0] < 12 ? parseInt(hora[0], 10) + 12 : hora[0],
      hora[1],
      hora[2]
    );

    if (json.EstadoComunicado === 'Enviado') {
      this._estado = ESTADO_MENSAJE.Sent;
    } else {
      if (json.EstadoComunicado === 'Leido') {
        this._estado = ESTADO_MENSAJE.Read;
      }
    }
  }
}
