import { Entity } from './entity';

export class Empresa extends Entity {

  get nombreLogo(): string { return this._nombreLogo; }

  set nombreLogo(nombreLogo: string) { this._nombreLogo = nombreLogo; }

  get razonSocial(): string { return this._razonSocial; }

  set razonSocial(razonSocial: string) { this._razonSocial = razonSocial; }

  get tipoDocumento(): string { return this._tipoDocumento; }

  set tipoDocumento(tipoDocumento: string) { this._tipoDocumento = tipoDocumento; }

  get idActividadEconomica(): number { return this._idActividadEconomica; }

  set idActividadEconomica(idActividadEconomica: number) {
    this._idActividadEconomica = idActividadEconomica;
  }

  get direccionSedePrincipal(): string { return this._direccionSedePrincipal; }

  set direccionSedePrincipal(direccionSedePrincipal: string) {
    this._direccionSedePrincipal = direccionSedePrincipal;
  }

  get documentoEmp(): string { return this._documentoEmp; }

  set documentoEmp(documentoEmp: string) { this._documentoEmp = documentoEmp; }

  get idMunicipio(): number { return this._idMunicipio; }

  set idMunicipio(value: number) { this._idMunicipio = value; }

  get idDepartamento(): number { return this._idDepartamento; }

  set idDepartamento(value: number) { this._idDepartamento = value; }

  get nombreDepartamento(): string { return this._nombreDepartamento; }

  set nombreDepartamento(value: string) { this._nombreDepartamento = value; }

  get nombreMunicipio(): string { return this._nombreMunicipio; }

  set nombreMunicipio(value: string) { this._nombreMunicipio = value; }

  get descripcionActividadEconomica(): string { return this._descripcionActividadEconomica; }

  set descripcionActividadEconomica(value: string) { this._descripcionActividadEconomica = value; }

  get correo(): string { return this._correo; }

  set correo(value: string) { this._correo = value; }

  get telefono(): string { return this._telefono; }

  set telefono(value: string) { this._telefono = value; }

  private _nombreLogo: string;

  private _razonSocial: string;

  private _tipoDocumento: string;

  private _idActividadEconomica: number;

  private _direccionSedePrincipal: string;

  private _documentoEmp: string;

  private _idMunicipio: number;

  private _idDepartamento: number;

  private _nombreDepartamento: string;

  private _nombreMunicipio: string;

  private _descripcionActividadEconomica: string;

  private _correo: string;

  private _telefono: string;

  constructor(json: any) {
    super(parseInt(json.documentoEmp, 10));

    this.razonSocial = json.razonSocial;
    this.tipoDocumento = json.tipoDocEmp;
    this.idActividadEconomica = json.idActividadEconomica;
    this.direccionSedePrincipal = json.dirEmpresa;
    this.documentoEmp = json.documentoEmp;
    this.idMunicipio = json.idMuniEmpresa;
    this.idDepartamento = json.idDeparEmpresa;
    this.nombreDepartamento = json.nomDepEmpresa;
    this.nombreMunicipio = json.nomMunEmpresa;
    this.descripcionActividadEconomica = json.actividadEconomica;
    this.correo = json.emailEmpresa;
    this.telefono = json.telefonoEmpresa;
  }

  static parse(json: any): Empresa {
    return new Empresa(json);
  }

  public parseNombreLogo(json: any): void {
    this._nombreLogo = json.NombreArchivo;
    console.log('json' + JSON.stringify(json));
    console.log(this._nombreLogo);
  }

}
