import { Injectable } from '@angular/core';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { DataBaseService } from './data-base.service';
import { Dispobible } from 'src/app/interfaces/dispobible';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { async } from '@angular/core/testing';

@Injectable({
  providedIn: 'root',
})
export class SaveDataService {
  private data: EDGuardarPermisoTrabajoAPP;
  constructor(private db: DataBaseService) {}

  setData(data: EDGuardarPermisoTrabajoAPP) {
    this.data = data;
  }

  getData(): EDGuardarPermisoTrabajoAPP {
    return this.data;
  }

  clearData() {
    this.data = null;
  }

  async saveData(): Promise<void> {
    try {
      this.db.open().then(() => {
        console.log('abriendo base de datos');
        console.log(this.data);
        this.db.addEDGuardarPermisoTrabajoAPP(this.data);
      });
    } catch (error) {
      console.error('Error al guardar el permiso de trabajo:', error);
      this.db.close();
    }
  }

  async porLiberar(trabajadores: TrabajadorMigrado[]): Promise<void> {
    try {
      this.db.open().then(async () => {
        await this.db.deleteAllPorLiberar();
        trabajadores.forEach(trabajador => {
          this.db.setPorLiberar(trabajador);
        });
      });
    } catch (error) {
      console.error('Error al guardar los trabajadores disponibles:', error);
      this.db.close();
    }
  }

  async updateTrabajador(trabajador: TrabajadorMigrado): Promise<void> {
    try {
      this.db.open().then(() => {
        this.db.updateTrabajadorMigrado(trabajador);
      });
    } catch (error) {
      console.error('Error al actualizar el trabajador:', error);
      this.db.close();
    }
  }

  async deleData(data: EDGuardarPermisoTrabajoAPP): Promise<void> {
    try {
      this.db.open().then(() => {
        this.db.deleteEDGuardarPermisoTrabajoAPP(data);
      });
    } catch (error) {
      console.error('Error al eliminar el permiso de trabajo:', error);
      this.db.close();
    }
  }
}
