import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MaestroPadre } from 'src/app/interfaces/maestro-padre';
import { MaestroHijo } from 'src/app/interfaces/maestro-hijo';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { MaestroCuestionario } from 'src/app/interfaces/maestro-cuestionario';
import { DataBaseService } from './data-base.service';
import { TipoPermiso } from 'src/app/interfaces/tipo-permiso';
import { EDInfoAuditoriaAPP } from 'src/app/interfaces/edinfo-auditoria-app';
import { ContextService } from '../context.service';
import { EDGuardarPermisoTrabajoAPP } from 'src/app/interfaces/edguardar-permiso-trabajo-app';
import { Actas } from 'src/app/interfaces/actas';
import { DataActa } from 'src/app/interfaces/data-acta';

@Injectable({
  providedIn: 'root',
})
export class DataService {
  constructor(private http: HttpClient, private db: DataBaseService, private context: ContextService) {}


  private PATH = this.context.getEnvUrl().call(null, 'participacion') + '/PermisoDeTrabajoServicio/';


  private INFO_TRABAJO = `${this.PATH}Consultar_Informacion-PermisoDeTrabajo`;

  private GET_TRABAJADOR = `${this.PATH}Validar-RelacionLaboralUsuarioMigrar`;

  private INFO_PERMISO_TRABAJO = `${this.PATH}listar-Permisos-Trabajo`;

  private LIBERAR_TRABAJADOR = `${this.PATH}Liberar_EmpleadosAPP`;

  private ENVIAR_PERMISO = `${this.PATH}Guardar_PermisoTrabajoAPP`;

  private LISTAR_ACTAS = `${this.PATH}listar-actas`;

  async infoTrabajo(intId: string): Promise<void> {
    try {
      console.log('user:');
      const user = await this.context.get();

      const body = {
        intIDPemiso: intId,
        strDocumentoEmpresa: user.empresa.id,
        strTipoDocEmpresa: user.empresa.tipoDocumento,
      };
      return new Promise((resolve, reject) => {
        this.db.open().then(() => {
          this.http.post(this.INFO_TRABAJO, body).subscribe(
            (data: any) => {
              console.log('Información de trabajo:', data);
              const listaMaestroPadres: MaestroPadre[] = data.listaMaestroPadres;
              const listaMaestroHijos: MaestroHijo[] = data.listaMaestroHijos;
              const listaTrabajadoresMigrados: TrabajadorMigrado[] = data.listaTrabajadoresMigrados;
              const listaMaestroCuestionario: MaestroCuestionario[] = data.listaMaestroCuestionario;
              const promises = [
                this.db.deleteAllData(),
                ...listaTrabajadoresMigrados.map(trabajador => this.db.addTrabajadorMigrado(trabajador)),
                ...listaMaestroPadres.map(padre => this.db.addMaeastroPadre(padre)),
                ...listaMaestroHijos.map(hijo => this.db.addMaestroHijo(hijo)),
                ...listaMaestroCuestionario.map(cuestionario => this.db.addMaestroCuestionario(cuestionario)),
              ];

              Promise.all(promises)
                .then(() => {
                  const now = new Date();
                  localStorage.setItem('lastSync', now.toString());
                  resolve();
                })
                .catch(error => {
                  reject(error);
                });
            },
            error => {
              reject(error);
            }
          );
        });
      });
    } catch (error) {
      console.error('Error al obtener los datos:', error);
      this.db.close();
    }
  }

  async getTrabajador(strDocumento: string, tipoDocumento: string): Promise<TrabajadorMigrado> {
    const user = await this.context.get();
    const body = {
      strTipoDocEmpresa: user.empresa.tipoDocumento,
      strNumIdentificacionEmpresa: user.empresa.id,
      strTipoDocumentacionUsuario: tipoDocumento,
      strNumIdentificacionUsuario: strDocumento,
    };

    console.log('body:', body);
    return new Promise((resolve, reject) => {
      this.http
        .post(this.GET_TRABAJADOR, body, {
          headers: {
            'Content-Type': 'application/json',
          },
        })
        .subscribe(
          (data: TrabajadorMigrado) => {
            console.log('Trabajador: web web ', JSON.stringify(data));
            if (data) {
              resolve(data);
            } else {
              resolve(null);
            }
          },
          error => {
            console.error('Error en la petición HTTP:', error);
            reject(error);
          }
        );
    });
  }

  async infoPermisoTrabajo(): Promise<void> {
    try {
      return new Promise((resolve, reject) => {
        this.db.open().then(() => {
          this.http
            .get(this.INFO_PERMISO_TRABAJO, {
              headers: {
                'Content-Type': 'application/json',
              },
            })
            .subscribe((data: TipoPermiso[]) => {
              const promises = [this.db.deleteAllTipoPermiso(), ...data.map(permiso => this.db.addTipoPermiso(permiso))];

              Promise.all(promises)
                .then(() => {
                  console.log('Todos los permisos agregados');
                  const now = new Date();
                  localStorage.setItem('lastSync', now.toString());
                  resolve();
                })
                .catch(error => {
                  console.log('Error al agregar los permisos:', error);
                  reject(error);
                });
            });
        });
      });
    } catch (error) {
      console.error('Error al obtener los datos:', error);
      this.db.close();
    }
  }

  async liberarTrabajador(id: number[], audi: EDInfoAuditoriaAPP): Promise<void> {
    const body = {
      intTipoPermiso: 1,
      listaIDTrabajadoreLiberar: id,
      edInfoAuditoria: audi,
    };
    try {
      return new Promise((resolve, reject) => {
        this.http
          .post(this.LIBERAR_TRABAJADOR, body, {
            headers: {
              'Content-Type': 'application/json',
            },
          })
          .subscribe(
            (data: TrabajadorMigrado[]) => {
              console.log('Trabajador liberado:', data);
              if (data) {
                this.db.open().then(async () => {
                  await this.db.deleteAllTrabajadoresMigrados();
                  await this.db.deleteAllPorLiberar();
                  data.forEach(async trabajador => {
                    await this.db.addTrabajadorMigrado(trabajador);
                    await this.db.setPorLiberar(trabajador);
                  });
                  resolve();
                });
              }
            },
            error => {
              console.error('Error en la petición HTTP:', error);
              reject(error);
            }
          );
      });
    } catch (error) {
      console.error('Error al obtener los datos:', error);
      this.db.close();
    }
  }
  /* enviar */

  /* promesa que retorna estatus code es 200 y data o 404 no found */
  async enviarPermisoTrabajo(dataPermiso: EDGuardarPermisoTrabajoAPP): Promise<{ status: number; data: any }> {
    const body = dataPermiso;
    console.log('body:', JSON.stringify(body));

    for (const key in body) {
      if (Object.hasOwnProperty.call(body, key)) {
        console.log(`body[${key}]:`, JSON.stringify(body[key]));
      }
    }
    // console.log(body);
    try {
      return new Promise((resolve, reject) => {
        this.http
          .post(this.ENVIAR_PERMISO, body, {
            headers: {
              'Content-Type': 'application/json',
            },
          })
          .subscribe(
            data => {
              console.log('Permiso enviado:', JSON.stringify(data));
              if (data) {
                resolve({ status: 200, data });
              } else {
                resolve({ status: 404, data: 'data not found' });
              }
            },
            error => {
              console.error('Error en la petición HTTP:', error);
              reject(error);
            }
          );
      });
    } catch (error) {
      console.error('Error al obtener los datos:', error);
    }
  }

  async listarActas(data: DataActa): Promise<{ status: number; data: Actas[] }> {
    try {
      return new Promise((resolve, reject) => {
        console.log('url', this.LISTAR_ACTAS);
        this.http
          .post(this.LISTAR_ACTAS, data, {
            headers: {
              'Content-Type': 'application/json',
            },
          })
          .subscribe(
            (data: Actas[]) => {
              console.log('Actas:', data);
              if (data) {
                resolve({ status: 200, data });
              } else {
                resolve({ status: 404, data: [] });
              }
            },
            error => {
              console.error('Error en la petición HTTP:', error);
              reject(error);
            }
          );
      });
    } catch (error) {}
  }
}
