  public db: IDBDatabase;
  private dbName = 'SST';

  constructor() {}

  open(): Promise<IDBDatabase> {
    if (this.db) {
      return Promise.resolve(this.db);
    }
    return new Promise((resolve, reject) => {
      const request = indexedDB.open(this.dbName, 42);

      request.onerror = event => {
        console.error('Error al abrir la base de datos:', event);
        reject(event);
      };

      /* crear tablas en indexdb */
      request.onupgradeneeded = event => {
        this.db = (event.target as IDBOpenDBRequest).result;
        if (!this.db.objectStoreNames.contains('trabajadores')) {
          const store = this.db.createObjectStore('trabajadores', { keyPath: 'PK_ID_PermisoAlturas_PersonasAMigrar' });
          store.createIndex('strNumeroDocumeto', 'strNumeroDocumeto', { unique: true });
          // this.db.deleteObjectStore('trabajadores');
        }
        if (!this.db.objectStoreNames.contains('maestroPadres')) {
          this.db.createObjectStore('maestroPadres', { keyPath: 'Pk_ID_PermisoAlturas_ItemProteccionPadre' });
          // this.db.deleteObjectStore('maestroPadres');
        }
        if (!this.db.objectStoreNames.contains('maestroHijos')) {
          this.db.createObjectStore('maestroHijos', { keyPath: 'Pk_ID_PermisoAlturas_ProteccionHijos' });
          // this.db.deleteObjectStore('maestroHijos');
        }
        if (!this.db.objectStoreNames.contains('maestroCuestionario')) {
          this.db.createObjectStore('maestroCuestionario', { keyPath: 'Pk_ID_MaestroCuestionarioHijos' });
          // this.db.deleteObjectStore('maestroCuestionario');
        }
        if (!this.db.objectStoreNames.contains('data')) {
          this.db.createObjectStore('data', { keyPath: 'uniqueId' });
          // this.db.deleteObjectStore('data');
        }
        if (!this.db.objectStoreNames.contains('porLiberar')) {
          this.db.createObjectStore('porLiberar', { keyPath: 'PK_ID_PermisoAlturas_PersonasAMigrar' });
          // this.db.deleteObjectStore('porLiberar');
        }
        if (!this.db.objectStoreNames.contains('tipoPermiso')) {
          this.db.createObjectStore('tipoPermiso', { keyPath: 'PK_ID_PermisoAlturas_MaestroTipoPermiso' });
        }
        /* permisos enviados */
        if (!this.db.objectStoreNames.contains('permisosEnviados')) {
          this.db.createObjectStore('permisosEnviados', { keyPath: 'uniqueId' });
        }
        /* QR EMERGENCIA */
        if (!this.db.objectStoreNames.contains('qrEmergencia')) {
          this.db.createObjectStore('qrEmergencia', { autoIncrement: true });
          // this.db.deleteObjectStore('qrEmergencia');
        }
      };

      request.onsuccess = event => {
        this.db = (event.target as IDBOpenDBRequest).result;
        resolve(this.db);
      };
    });
  }

  /* close db  */
  close(): void {
    if (!this.db) return;
    this.db.close();
  }

