  getAllTrabajadoresMigrados(): Observable<TrabajadorMigrado[]> {
    return new Observable(observer => {
      const transaction = this.db.transaction(['trabajadores'], 'readonly');
      const store = transaction.objectStore('trabajadores');
      const request = store.getAll();
      request.onsuccess = () => {
        if (request.result === undefined) {
          observer.error('No se encontró el maestro padre');
          return;
        }
        observer.next(request.result);
        observer.complete();
      };

      request.onerror = () => {
        observer.error(request.error);
      };
    });
  }

