  getTrabajador(numeroDeDocumento: string, tipoDeDocumento: string): Promise<TrabajadorMigrado> {
    return new Promise((resolve, reject) => {
      const transaction = this.db.transaction(['trabajadores'], 'readonly');
      const store = transaction.objectStore('trabajadores');
      const index = store.index('strNumeroDocumeto');
      const request = index.get(numeroDeDocumento);
      request.onsuccess = () => {
        if (request.result === undefined) {
          resolve(null);
          return;
        }
        if (request.result.strTipoDocumento !== tipoDeDocumento) {
          resolve(null);
          return;
        }
        console.log('getTrabajador', request.result);
        resolve(request.result);
      };

      request.onerror = () => {
        reject(request.error);
      };
    });
  }

