import { AfterViewInit, Component, Input, OnDestroy } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { BarcodeScanner } from '@capacitor-community/barcode-scanner';

@Component({
  selector: 'app-qr-emergencia',
  templateUrl: './qr-emergencia.component.html',
  styleUrls: ['./qr-emergencia.component.scss'],
})
export class QrEmergenciaComponent implements OnDestroy, AfterViewInit{
  result: any = null;
  scanActive: boolean = false;
  @Input() app: string;

  constructor(private modalCtrl: ModalController, private alertCtl: AlertController) {}


  ngAfterViewInit(): void {
    // BarcodeScanner.prepare();
  }

  ngOnDestroy(): void {
    this.stopBarcodeRemove('');
  }

  async startBarcode(): Promise<void> {
    const allowed = await this.checkPermission();

    if (!allowed) {
      return;
    }

    const body = document.querySelector('body');
    body.style.setProperty('--background', 'transparent', 'important');
    body.style.setProperty('--ion-background-color', 'transparent', 'important');

    this.scanActive = true;
    const result = await BarcodeScanner.startScan(); 
    const app =  document.querySelector("app-ingreso-sede") as HTMLElement;
    

    app.style.opacity = "0"

    if (result.hasContent) {
      console.log('result', JSON.stringify(result));
      this.result = result.content;
      this.stopBarcodeRemove(result.content);
    }
  }

  async checkPermission(): Promise<boolean> {
    return new Promise(async (resolve, reject) => {
      // Check camera permission
      // This is just a simple example, check out the better checks below
      const status = await BarcodeScanner.checkPermission({ force: true });
      if (status.granted) {
        resolve(true);
        return;
      }

      if (status.denied) {
        const alert = await this.alertCtl.create({
          header: 'Sin autorización',
          message: 'Por favor permita el acceso a la cámara en su configuración',
          buttons: [
            {
              text: 'No',
              role: 'cancel',
            },
            {
              text: 'Abrir configuración',
              handler: () => {
                BarcodeScanner.openAppSettings();
                resolve(false);
              },
            },
          ],
        });

        await alert.present();
        return;
      }

      resolve(false);
      return;
    });
  }

  stopBarcodeRemove(msg: any) {
    try {
      BarcodeScanner.stopScan();
      BarcodeScanner.showBackground();
      this.scanActive = false;
      const body = document.querySelector('body');
      body.style.removeProperty('--background');
      body.style.removeProperty('--ion-background-color');
      body.style.backgroundColor = '';
      body.style.opacity = '';

      console.log("deospues")
      const app =  document.querySelector("app-ingreso-sede") as HTMLElement;

      app.style.opacity = "1"
      // app2.style.opacity = '1';

      this.modalCtrl.dismiss({
        response: msg ? msg : null,
      });
    } catch (e) {
      console.error('Error al cerrar el escaner', e);
    }
  }

  cerrarModal() {
    return this.modalCtrl.dismiss();
  }
}
