import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { OneSignal } from '@ionic-native/onesignal/ngx';
import { from, Observable } from 'rxjs';
import { mergeMap } from 'rxjs/operators';
import { ESTADO_MENSAJE } from '../commons/enum-estado-mensaje';
import { ContextService } from './context.service';

@Injectable({
  providedIn: 'root'
})
export class MensajeService {

  constructor(
    private context: ContextService,
    private http: HttpClient,
    private oneSignal: OneSignal
  ) { }

  async getUrlByMensajeState(estadoMensaje: ESTADO_MENSAJE): Promise<string> {
    const context = await this.context.get();
    const idTrabajador = context.trabajador.id;
    return this.context.getEnvUrl().call(null, 'plananual')
      + '/ComunicadosAPP/Listar-Comunicados-Usuario-Por-Estado'
      + '?Estado=' + estadoMensaje
      + '&IdentificacionUsuario=' + idTrabajador;
  }

  getByMensajeState(estadoMensaje: ESTADO_MENSAJE): Observable<any> {
    const promise = this.getUrlByMensajeState(estadoMensaje).then(p => p);
    console.log(promise)
    return from(promise).pipe(mergeMap(url => this.http.get(url)));
  }

  updateMensajeState(idMensaje: number, state: ESTADO_MENSAJE): Observable<any> {
    const url = this.context.getEnvUrl().call(null, 'plananual')
      + '/ComunicadosAPP/Actualizar-Usuario-Comunicado-APP'
      + '?PK_Id_Mensaje=' + idMensaje
      + '&Estado=' + state;
    return this.http.get(url);
  }

  updateOneSignalPlayerId(idPlayer: string): Observable<any> {
    const promise = this.context.get().then(c => {
      const idTrabajador = c.trabajador.id;
      const url = this.context.getEnvUrl().call(null, 'plananual')
        + '/ComunicadosAPP/Actualizar-PlayerID-Comunicado-APP'
        + '?IdentificacionUsuario=' + idTrabajador
        + '&PlayerID=' + idPlayer;
      return url;
    });

    return from(promise).pipe(mergeMap(url => this.http.get(url)));
  }

}
