export class Entity {

  private _id: number;

  constructor(id?: number) {
    if (id == null) {
      return;
    }

    this.id = id;
  }

  get id(): number { return this._id; }

  set id(id: number) { this._id = id; }

  static compare(e1: Entity, e2: Entity): boolean {
    return e1 && e2
      ? e1.id === e2.id
      : e1 === e2;
  }

}
