import { Component, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { AlertController, ModalController, NavParams } from '@ionic/angular';
import { IonToggle } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { switchMap } from 'rxjs/operators';
import { DataQrEmergencia } from 'src/app/interfaces/data-qr-emergencia';
import { QrEmergenciaService } from 'src/app/services/qr-emergencia.service';
import { UtilsService } from 'src/app/services/utils.service';

@Component({
  selector: 'app-modal-data-emergencia',
  templateUrl: './modal-data-emergencia.component.html',
  styleUrls: ['./modal-data-emergencia.component.scss'],
})
export class ModalDataEmergenciaComponent implements OnInit, OnDestroy {
  dataQr: DataQrEmergencia;
  fechaYHora: string;
  accionEmergencia: string;
  showAlertSubscriptionIngreso: Subscription;
  showAlertSubscriptionEgreso: Subscription;
  showAlertSendDataSucces: Subscription;
  showAlertSendDataFail: Subscription;
  @ViewChild(IonToggle) toggle: IonToggle;

  constructor(
    public modalController: ModalController,
    private navParams: NavParams,
    private utils: UtilsService,
    private alertController: AlertController,
    private qrEmergenciaService: QrEmergenciaService
  ) {}

  ngOnInit() {
    this.dataQr = this.navParams.get('dataQr');
    this.suscribirAlertaIngreso();
    this.fechaYHora = new Date().toISOString();
    this.getCurrentLocation();
  }

  ngOnDestroy() {
    if (this.showAlertSubscriptionIngreso) {
      this.showAlertSubscriptionIngreso.unsubscribe();
    }
    if (this.showAlertSubscriptionEgreso) {
      this.showAlertSubscriptionEgreso.unsubscribe();
    }
    if (this.showAlertSendDataSucces) {
      this.showAlertSendDataSucces.unsubscribe();
    }
  }

  dismissModal() {
    this.modalController.dismiss();
  }

  suscribirAlertaIngreso() {
    this.showAlertSubscriptionIngreso = this.qrEmergenciaService.showAlert
      .pipe(
        switchMap(() =>
          this.mostrarAlertaConfirmacion('¿Desea confirmar la salida?', 'Alerta', () =>
            this.qrEmergenciaService.ingresoNuevaSede(this.dataQr)
          )
        )
      )
      .subscribe();
    this.showAlertSubscriptionEgreso = this.qrEmergenciaService.showAlertEgreso
      .pipe(
        switchMap(() =>
          this.mostrarAlertaConfirmacion('¿Desea confirmar la salida?', 'Alerta', () =>
            this.qrEmergenciaService.egresoNuevaSede(this.dataQr)
          )
        )
      )
      .subscribe();
    this.showAlertSendDataSucces = this.qrEmergenciaService.showAlertSendDataSucces.subscribe(envioIngreso => {
      this.mostrarError(
        envioIngreso ? 'Se ha generado la notificación con éxito' : 'No se ha generado la notificación con éxito',
        envioIngreso ? 'Confirmación' : 'Alerta'
      );
    });
  }

  async onToggleChange(event) {
    try {
      if (event.detail.checked) {
        await this.getCurrentLocation();
      } else {
        // TODO :  limpiar latitud y longitud
        this.dataQr.latitud = '';
        this.dataQr.longitud = '';
      }
    } catch (error) {
      console.log(error);
    } finally {
      this.utils.dismissLoading();
    }
  }

  async getCurrentLocation() {
    try {
      await this.utils.presentLoading();
      navigator.geolocation.getCurrentPosition(
        position => {
          console.log(`Latitude: ${position.coords.latitude}, Longitude: ${position.coords.longitude}`);
          this.dataQr.latitud = position.coords.latitude.toString();
          this.dataQr.longitud = position.coords.longitude.toString();
          this.toggle.checked = true;
        },
        error => {
          console.log('Error getting location', JSON.stringify(error));
          this.toggle.checked = false;
          this.mostrarError('No pudimos capturar tú ubicación vuelve a intentarlo');
        },
        { timeout: 2000 }
      );
    } catch (error) {
      console.log(error);
    } finally {
      this.utils.dismissLoading();
    }
  }

  async confirmarAcceso() {
    try {
      if (!this.accionEmergencia) {
        this.mostrarError('El campo acción es requerido');
        return;
      }
      if (
        this.dataQr.latitud === '' ||
        this.dataQr.longitud === '' ||
        this.dataQr.latitud === undefined ||
        this.dataQr.longitud === undefined
      ) {
        this.mostrarError('Debes darnos tú ubicación para poder continuar');
        return;
      }
      await this.utils.presentLoading();
      this.dataQr.FechaYHoraIngreso = this.fechaYHora;
      this.dataQr.accion = this.accionEmergencia;

      this.qrEmergenciaService.setQrEmergencia(this.dataQr);
    } catch (error) {
      console.log(error);
    } finally {
      this.utils.dismissLoading();
    }
  }

  private async mostrarError(message: string, header?: string) {
    const alert = await this.alertController.create({
      header: header || 'Alerta',
      message: message,
      buttons: ['OK'],
    });
    alert.present();
  }

  /* alerta para confirmar accion */
  private async mostrarAlertaConfirmacion(message: string, header?: string, fun?: () => Promise<void>) {
    const alert = await this.alertController.create({
      header: header || 'Alerta',
      message: message,
      buttons: [
        {
          text: 'Cancelar',
          role: 'cancel',
          handler: () => {},
        },
        {
          text: 'Aceptar',
          handler: async () => {
            await fun();
          },
        },
      ],
    });
    alert.present();
  }

  /* formato fecha */
  public getFechaYHora(): string {
    const fechaYHora = new Date(this.fechaYHora);

    const dia = String(fechaYHora.getDate()).padStart(2, '0');
    const mes = String(fechaYHora.getMonth() + 1).padStart(2, '0'); // Los meses en JavaScript comienzan desde 0
    const ano = fechaYHora.getFullYear();

    const horas = fechaYHora.getHours() > 12 ? fechaYHora.getHours() - 12 : fechaYHora.getHours();
    const minutos = String(fechaYHora.getMinutes()).padStart(2, '0');
    const segundos = String(fechaYHora.getSeconds()).padStart(2, '0');
    const ampm = fechaYHora.getHours() >= 12 ? 'PM' : 'AM';

    const fechaFormateada = `${dia}/${mes}/${ano} ${horas}:${minutos} : ${segundos} ${ampm}`;

    return fechaFormateada;
  }
}
