import { Pipe, PipeTransform } from '@angular/core';
import { MONTHS_SHORT } from '../commons/locale';

@Pipe({
  name: 'dateOrHour'
})
export class DateOrHourPipe implements PipeTransform {

  transform(fechaHora: Date): string {
    const fechaHoy: Date = new Date();
    let dateHourString: string;

    if (this.compareDates(fechaHora, fechaHoy) !== -1) {
      dateHourString = fechaHora.getHours() + ':';
      if (fechaHora.getMinutes() < 10) {
        dateHourString += '0' + fechaHora.getMinutes();
      } else {
        dateHourString += fechaHora.getMinutes();
      }
    }
    else {
      dateHourString = fechaHora.getDate() + ' ' + MONTHS_SHORT[fechaHora.getMonth()];
    }
    return dateHourString;
  }

  compareDates(d1: Date, d2: Date): number {
    const d1Value = 10000 * d1.getFullYear() + 100 * d1.getMonth() + d1.getDate();
    const d2Value = 10000 * d2.getFullYear() + 100 * d2.getMonth() + d2.getDate();

    if (d1Value > d2Value) {
      return 1;
    }
    else {
      if (d1Value < d2Value) {
        return -1;
      } else {
        return 0;
      }
    }
  }

}
