import { Component, OnInit, AfterViewInit, ViewChild, ElementRef, Output, EventEmitter } from '@angular/core';
import SignaturePad from 'signature_pad';

@Component({
  selector: 'app-signature-pad',
  templateUrl: './signature-pad.component.html',
  styleUrls: ['./signature-pad.component.scss'],
})
export class SignaturePadComponent implements OnInit, AfterViewInit {
  @ViewChild('canvas', { static: true }) signaturePadElement: ElementRef;
  signaturePad: any;
  canvasWidth: number;
  canvasHeight: number;
  guardado = false;

  constructor() {}
  ngAfterViewInit(): void {
    this.signaturePad = new SignaturePad(this.signaturePadElement.nativeElement);
  }

  @Output() onSignatureChange: EventEmitter<string> = new EventEmitter<string>();
  @Output() onSignatureChange2: EventEmitter<string> = new EventEmitter<string>();
  @Output() onSignatureChange3: EventEmitter<string> = new EventEmitter<string>();

  ngOnInit(): void {
    this.init();
  }

  init() {
    const canvas: any = document.querySelectorAll('canvas');
    canvas.forEach(element => {
      element.width = window.innerWidth;
      element.height = 150;
    });
    if (this.signaturePad) {
      this.signaturePad.clear(); // Clear the pad on init
    }
  }

  isCanvasBlank(): boolean {
    if (this.signaturePad) {
      return this.signaturePad.isEmpty() ? true : false;
    }
  }

  clear() {
    if (this.guardado) {
      this.signaturePad.clear();
      this.guardado = false;
      this.signaturePad.on();
    }
  }

  undo() {
    const data = this.signaturePad.toData();
    if (data) {
      data.pop(); // remove the last dot or line
      this.signaturePad.fromData(data);
    }
  }

  save() {
    if (!this.guardado) {
      const data = this.signaturePad.toDataURL();
      const cadenaLimpia = data.split(',')[1];
      this.onSignatureChange.emit(cadenaLimpia);
      this.guardado = true;
      this.signaturePad.off();
    }
  }
}
