import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'groupsOfThree'
})
export class GroupsOfThreePipe implements PipeTransform {

  transform(value: string) {
    // porque la variable value es de tipo number?
    const strValue: string = String(value);
    const reminderIndex: number = strValue.length % 3;
    let transformedValue: string = strValue.substr(0, reminderIndex);

    for (let i = reminderIndex; i < strValue.length; i += 3) {
      transformedValue += ' ' + strValue.substr(i, 3);
    }

    return transformedValue;
  }

}
