import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'auth',
    loadChildren: () => import('src/app/pages/autenticacion/autenticacion.module').then(m => m.AutenticacionPageModule),
  },
  {
    path: 'asistencia-eventos',
    loadChildren: () => import('./app/pages/asistencia-eventos/asistencia-eventos.module').then(m => m.AsistenciaEventosPageModule),
  },
  {
    path: 'home',
    loadChildren: () => import('src/app/pages/home/home.module').then(m => m.HomePageModule),
  },
  {
    path: 'settings',
    loadChildren: () => import('src/app/pages/configuracion/configuracion.module').then(m => m.ConfiguracionPageModule),
  },
  {
    path: 'communications',
    loadChildren: () => import('src/app/pages/comunicaciones/comunicaciones.module').then(m => m.ComunicacionesPageModule),
  },
  {
    path: 'accountability',
    loadChildren: () => import('src/app/pages/redicion-cuentas/redicion-cuentas.module').then(m => m.RedicionCuentasPageModule),
  },
  {
    path: 'sst-inquiries',
    loadChildren: () => import('src/app/pages/consultas/consultas.module').then(m => m.ConsultasPageModule),
  },
  {
    path: 'risks',
    loadChildren: () => import('src/app/pages/riesgos/riesgos.module').then(m => m.RiesgosPageModule),
  },
  {
    path: 'health-conditions',
    loadChildren: () => import('src/app/pages/condiciones-salud/condiciones-salud.module').then(m => m.CondicionesSaludPageModule),
  },
  {
    path: 'epp',
    loadChildren: () => import('src/app/pages/epp/epp.module').then(m => m.EppPageModule),
  },
  {
    path: 'epp-details',
    loadChildren: () => import('src/app/pages/epp-detalle/epp-detalle.module').then(m => m.EppDetallePageModule),
  },
  {
    path: 'incidents',
    loadChildren: () => import('src/app/pages/incidentes/incidentes.module').then(m => m.IncidentesPageModule),
  },
  {
    path: 'unsafe-conditions',
    loadChildren: () =>
      import('src/app/pages/actos-condiciones-inseguras/actos-condiciones-inseguras.module').then(
        m => m.ActosCondicionesInsegurasPageModule
      ),
  },
  {
    path: 'sst-advisory',
    loadChildren: () => import('src/app/pages/asesoria-sst/asesoria-sst.module').then(m => m.AsesoriaSstPageModule),
  },
  {
    path: 'inicio-emergencia',
    loadChildren: () => import('./app/pages/emergencia/inicio-emergencia/inicio-emergencia.module').then(m => m.InicioEmergenciaPageModule),
  },
  {
    path: '**',
    redirectTo: 'auth',
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { preloadingStrategy: PreloadAllModules })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
