import { Component, Input, OnInit } from '@angular/core';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { AlertController } from '@ionic/angular';
import { Subscription } from 'rxjs';
import { ESTADO_MENSAJE } from 'src/app/commons/enum-estado-mensaje';
import { alertCommunicationError } from 'src/app/commons/user-feedback';
import { Mensaje } from 'src/app/entities/mensaje';
import { MensajeService } from 'src/app/services/mensaje.service';

@Component({
  selector: 'app-mensaje-lista',
  templateUrl: './mensaje-lista.component.html',
  styleUrls: ['./mensaje-lista.component.scss'],
})
export class MensajeListaComponent implements OnInit {

  @Input()
  filterFunction: any;

  mensajes: Mensaje[];

  private leerMensajeSubscription: Subscription;

  private eliminarMensajeSubscription: Subscription;

  private indexShowMore: number;

  constructor(
    private mensajeService: MensajeService,
    private alertCtrl: AlertController,
    private browser: InAppBrowser
  ) {
    this.indexShowMore = -1;
  }

  ngOnInit(): void {
    this.mensajeService.getByMensajeState(ESTADO_MENSAJE.Sent).subscribe(
      (response: any) => {
        this.mensajeService.getByMensajeState(ESTADO_MENSAJE.Read).subscribe(
          (_response: any) => {
            this.mensajes = Mensaje.parse(response)
              .concat(Mensaje.parse(_response))
              .filter(this.filterFunction)
              .sort(this.sortByDatePredicate);
          },
          (error: any) => {
           alertCommunicationError(this.alertCtrl);
          }
        );
      },
      (error: any) => {
       alertCommunicationError(this.alertCtrl);
      }
    );
  }

  sortByDatePredicate(m1: Mensaje, m2: Mensaje): number {
    return m2.fechaHoraEnvio.getTime() - m1.fechaHoraEnvio.getTime();
  }

  isLoading(): boolean {
    return !this.mensajes;
  }

  showMore(index: number, mensaje?: Mensaje): void {
    if (index === this.indexShowMore) {
      this.indexShowMore = -1;
    } else {
      this.indexShowMore = index;
      if (mensaje) {
        this.leerMensaje(mensaje);
      }
    }
  }

  isShowingMore(index: number): boolean {
    return (index === this.indexShowMore);
  }

  leerMensaje(mensaje: Mensaje): void {
    mensaje.estado = ESTADO_MENSAJE.Read;

    if (this.leerMensajeSubscription) {
      this.leerMensajeSubscription.unsubscribe();
    }

    this.leerMensajeSubscription = this.mensajeService
      .updateMensajeState(mensaje.id, ESTADO_MENSAJE.Read).subscribe(
        (response: any) => { },
        (error: any) => alertCommunicationError(this.alertCtrl)
      );
  }

  isMensajeRead(estado: ESTADO_MENSAJE): boolean {
    return estado === ESTADO_MENSAJE.Read;
  }

  eliminarMensaje(idMensaje: number): void {
  
    this.eliminarMensajeSubscription = this.mensajeService
      .updateMensajeState(idMensaje, ESTADO_MENSAJE.Deleted).subscribe(
        (response: any) => {
          this.mensajes = this.mensajes.filter((mensaje: Mensaje) => {
            return mensaje.id !== idMensaje;
          });
        },
        (error: any) => alertCommunicationError(this.alertCtrl)
     );
  }

  noData(): boolean {
    return this.mensajes && this.mensajes.length === 0;
  }

  showInBrowser(url: string): void {
    const target = '_system';
    const options = '';
    this.browser.create(url, target, options);
  }

  isLink(str: string): boolean {
    return str.includes('<a') && str.includes('</a>');
  }

  extractLinkName(str: string): string {
    return str.split('>')[1].split('<')[0];
  }

  extractLinkUrl(str: string): string {
    return str.split('href=\"')[1].split('\"')[0];
  }

  tokenizeMessage(message: string): string[] {
    const startIndex = message.indexOf('<a');
    const endIndex = message.indexOf('</a>') + 4;
    if (startIndex === -1) {
      return [message];
    } else {
      return [message.substring(0, startIndex)
        , message.substring(startIndex, endIndex)]
        .concat(this.tokenizeMessage(message.substring(endIndex + 1)));
    }
  }

}
