import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage';
import * as Environment from 'src/app/commons/environment';
import { Context } from '../entities/context';

@Injectable({
  providedIn: 'root',
})
export class ContextService {
  private context: Context;

  private envFunct: any;

  private alisstaWeb: string;

  private googleProjectKeyFunct: string;

  private oneSignalApiKeyFunct: string;

  /**
   * Dirección _URL_ para el descubriento de la _IP_ pública.
   */
  private ipApi: string;

  /**
   * Indica si actualmente se está recuperando la sesión.
   */
  private isFetching: boolean;

  constructor(private storage: Storage) {
    this.recoverSession();
  }

  async get(): Promise<Context> {
    if (this.isFetching) {
      await this.wait(1000);

      return await this.get();
    }

    return this.context;
  }

  set(json: any): void {
    this.context = new Context(json);

    // Almacena la sesión
    this.storage.set('session', json);
  }

  getEnvUrl(): any {
    return this.envFunct;
  }

  getAlisstaWeb(): string {
    return this.alisstaWeb;
  }

  getOneSignalAppKey(): string {
    return this.oneSignalApiKeyFunct;
  }

  getGoogleProjectApiKey(): string {
    return this.googleProjectKeyFunct;
  }

  /**
   * Obtiene la _URL_ para el descubrimiento de la _IP_ pública.
   */
  getIPApi(): string {
    return this.ipApi;
  }

  setEnvironment(environment: Environment.Environments): void {
    console.log('-----------------------', environment, this.context);
    switch (environment) {
      case Environment.Environments.Test:
        this.envFunct = Environment.getAlisstaTest;
        this.alisstaWeb = Environment.getAlisstaWebTest();
        this.googleProjectKeyFunct = Environment.getGoogleProjectKeyTest();
        this.oneSignalApiKeyFunct = Environment.getOneSignalKeyTest();
        break;
      case Environment.Environments.PreProd:
        this.envFunct = Environment.getAlisstaPreProd;
        break;
      case Environment.Environments.Prod:
        this.envFunct = Environment.getAlisstaProd;
        this.alisstaWeb = Environment.getAlisstaWebProd();
        this.googleProjectKeyFunct = Environment.getGoogleProjectKeyProd();
        this.oneSignalApiKeyFunct = Environment.getOneSignalKeyProd();
        break;
      case Environment.Environments.V2019:
        this.envFunct = Environment.getAlisstaV2019;
        this.alisstaWeb = Environment.getAlisstaWebV2019();
        this.googleProjectKeyFunct = Environment.getGoogleProjectKeyTest();
        this.oneSignalApiKeyFunct = Environment.getOneSignalKeyTest();
        break;
    }
    console.log('-----------------------', this.envFunct, this.alisstaWeb);

    this.ipApi = Environment.getIPApi();
  }

  /**
   * Recupera la sesión del usuario, si existe, del almacén de datos.
   */
  private async recoverSession(): Promise<void> {
    this.isFetching = true;
    const session = await this.storage.get('session');

    if (!session) {
      // No hay sesión registrada.

      this.isFetching = false;

      return;
    }

    this.context = new Context(session);
    this.isFetching = false;
  }

  /**
   * Método utilitario que permite retrasar la ejecución del flujo.
   *
   * @param timeout Tiempo de espera.
   */
  private wait(timeout: number): Promise<any> {
    return new Promise(resolve => {
      setTimeout(resolve, timeout);
    });
  }
}
