import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { LoadingController } from '@ionic/angular';
import { Observable } from 'rxjs';
import { ContextService } from './context.service';

@Injectable({
  providedIn: 'root',
})
export class UtilsService {
  private loading: HTMLIonLoadingElement;

  constructor(private loadingCtrl: LoadingController, private context: ContextService, private http: HttpClient) {}

  async presentLoading(): Promise<void> {
    this.dismissLoading();

    this.loading = await this.loadingCtrl.create({
      message: 'Cargando...',
    });

    await this.loading.present();
  }

  dismissLoading(): void {
    if (!this.loading) {
      return;
    }

    this.loading.dismiss();
    this.loading = undefined;
  }

  /**
   * Obtiene la dirección IP pública desde la cual se realizan las peticiones.
   */
  getIPAddress(): Observable<any> {
    const url = this.context.getIPApi();

    return this.http.get(url);
  }

  getDaysDifference(date1: Date, date2: Date): number {
    // Un día en milisegundos
    const oneDay = 1000 * 60 * 60 * 24;
    // Calculando la diferencia en tiempo entre dos fechas
    const diffInTime = date2.getTime() - date1.getTime();
    // Calculando la diferencia en días entre dos fechas
    const diffInDays = Math.round(diffInTime / oneDay);
    return diffInDays;
  }
}
