import { Injectable } from '@angular/core';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';

@Injectable({
  providedIn: 'root',
})
export class TablaTrabajadoresService {
  constructor() {}
  private tablaTrabajadores: TrabajadorMigrado[] = [];

  setListaTrabajadores(trabajadores: TrabajadorMigrado[]) {
    this.tablaTrabajadores = trabajadores;
  }

  getTablaTrabajadores(): TrabajadorMigrado[] {
    if (this.tablaTrabajadores === undefined) {
      return [];
    }
    return this.tablaTrabajadores;
  }

  addTrabajador(trabajador: TrabajadorMigrado): void {
    /* actualizar el dato que viene con uno que ya esxiste */
    if (trabajador.strNombreTrabajador === '') return;

    const existe = this.tablaTrabajadores.some(t => t.strNumeroDocumeto === trabajador.strNumeroDocumeto);
    if (existe) {
      // Encontrar el índice del trabajador existente
      const index = this.tablaTrabajadores.findIndex(t => t.strNumeroDocumeto === trabajador.strNumeroDocumeto);

      if (index !== -1) {
        // Reemplazar el trabajador existente con el trabajador actualizado
        this.tablaTrabajadores[index] = trabajador;

        // Mover el trabajador al principio de la lista
        this.tablaTrabajadores = [trabajador, ...this.tablaTrabajadores.slice(0, index), ...this.tablaTrabajadores.slice(index + 1)];
      }
    } else {
      // Si el trabajador no existe, agrégalo al principio
      this.tablaTrabajadores = [trabajador, ...this.tablaTrabajadores];
    }
  }

  deleteTrabajador(trabajador: TrabajadorMigrado): void {
    this.tablaTrabajadores = this.tablaTrabajadores.filter(t => t.strNumeroDocumeto !== trabajador.strNumeroDocumeto);
  }

  deleteAll(): void {
    this.tablaTrabajadores = [];
  }
}
