import { AlertController } from '@ionic/angular';

export async function presentAlert(title: string, subTitle: string, message: string, alertCtrl: AlertController): Promise<void> {
  const alert = await alertCtrl.create({
    header: title,
    subHeader: subTitle,
    message,
    buttons: [{ text: 'Aceptar', role: 'null' }],
    backdropDismiss: true,
  });

  await alert.present();
}

export function alertCommunicationError(alertCtrl: AlertController): void {
  const msg = 'No se logró la conexión. Intente más tarde.';
  presentAlert('Alerta', '', msg, alertCtrl);
}
