import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AlertController, ModalController, Platform } from '@ionic/angular';
import { TrabajadorMigrado } from 'src/app/interfaces/trabajador-migrado';
import { SaveDataService } from 'src/app/services/SST/save-data.service';
import { TablaTrabajadoresService } from 'src/app/services/SST/tabla-trabajadores.service';

@Component({
  selector: 'app-modal-trabajadores',
  templateUrl: './modal-trabajadores.component.html',
  styleUrls: ['./modal-trabajadores.component.scss'],
})
export class ModalTrabajadoresComponent implements OnInit {
  trabajadores: TrabajadorMigrado[];
  pendietesEliminar: TrabajadorMigrado[] = [];
  constructor(
    public modalController: ModalController,
    private tablaTrabajadoresService: TablaTrabajadoresService,
    private saveDataService: SaveDataService,
    private router: Router,
    private allertController: AlertController
  ) {}

  ngOnInit() {
    this.trabajadores = this.tablaTrabajadoresService.getTablaTrabajadores();
    if (this.trabajadores.length == 0) {
      this.router.navigate(['/home/permisos']);
    }
  }

  dismissModal() {
    this.pendietesEliminar = [];
    this.modalController.dismiss();
  }

  eliminarTrabajador(trabajador: TrabajadorMigrado) {
    trabajador.blnActivo = false;
    this.allertController
      .create({
        header: 'Eliminar trabajador',
        message: '¿Está seguro que desea eliminar este trabajador?',
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel',
            handler: () => {
              console.log('Cancelado');
            },
          },
          {
            text: 'Aceptar',
            handler: async () => {
              let data = this.saveDataService.getData();
              console.log('Eliminado');
              this.tablaTrabajadoresService.deleteTrabajador(trabajador);
              this.trabajadores = this.tablaTrabajadoresService.getTablaTrabajadores();
              data['listaTrabajadoresMigrados'] = this.tablaTrabajadoresService.getTablaTrabajadores();
              await this.saveDataService.updateTrabajador(trabajador);
              await this.saveDataService.saveData();
              if (this.trabajadores.length === 0) {
                this.confirmDel();
              }
            },
          },
        ],
      })
      .then(alert => {
        alert.present();
      });
  }

  async confirmDel() {
    try {
      let data = this.saveDataService.getData();
      if (this.trabajadores.length == 0) {
        await this.saveDataService.deleData(data);
        await this.saveDataService.porLiberar(this.pendietesEliminar);
        this.modalController.dismiss();
        /* navegar a home y no volver a esta pagina */
        // this.router.navigateByUrl('/home', { replaceUrl: true });
        this.router.navigate(['/home/permisos']);
        return;
      }
      console.log(data);
      data['listaTrabajadoresMigrados'] = this.tablaTrabajadoresService.getTablaTrabajadores();
      this.saveDataService.setData(data);
      await this.saveDataService.saveData();
      await this.saveDataService.porLiberar(this.pendietesEliminar);
      this.modalController.dismiss();
    } catch (error) {
      console.error('Error al guardar los trabajadores disponibles:', error);
      this.modalController.dismiss();
    }
  }
}
