import { AfterViewInit, Component, Input } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-menu-operativo',
  templateUrl: './menu-operativo.component.html',
  styleUrls: ['./menu-operativo.component.scss'],
})
export class MenuOperativoComponent implements AfterViewInit {
  @Input()
  opcionSeleccionada: string;

  readonly PAGES_MAP = {
    auth: '/auth',
    asistenciaEventos: '/asistencia-eventos',
    home: '/home',
    settings: '/settings',
    communications: '/communications',
    accountability: '/accountability',
    sstInquiries: '/sst-inquiries',
    risks: '/risks',
    healthConditions: '/health-conditions',
    epp: '/epp',
    incidents: '/incidents',
    unsafeConditions: '/unsafe-conditions',
    sstAdvisory: '/sst-advisory',
  };

  constructor(private router: Router) {}

  ngAfterViewInit(): void {
    if (this.opcionSeleccionada) {
      setTimeout(() => this.makeOptionVisible(this.opcionSeleccionada), 1000);
    }
  }

  pushPage(page: string): void {
    this.opcionSeleccionada = page;
    this.router.navigate([this.PAGES_MAP[page]]);
  }

  getStyleClass(opcion: string): string {
    return opcion === this.opcionSeleccionada ? 'opcion-seleccionada' : 'opcion-no-seleccionada';
  }

  isOpcionSeleccionada(opcion: string): boolean {
    return opcion === this.opcionSeleccionada;
  }

  makeOptionVisible(opcionSeleccionada: string): void {
    let _opcionSeleccionada: string;

    if (opcionSeleccionada === 'incidents') {
      _opcionSeleccionada = 'unsafeConditions';
    } else {
      _opcionSeleccionada = opcionSeleccionada;
    }

    const element: HTMLElement = document.getElementById(_opcionSeleccionada);
    element.scrollIntoView({ behavior: 'smooth' });
  }
}
